 /* -------------------------------------------------------------------------- */
/*                                  Animation                                 */
/* -------------------------------------------------------------------------- */
/* -------------------------------- Slide up -------------------------------- */
.slide-up-enter {
  transform: translateY(100%);
}
.slide-up-enter-active {
  transform: translateY(0%);
  transition: transform 200ms;
  will-change: transform;
}
.slide-up-exit {
  transform: translateY(0%);
}
.slide-up-exit-active {
  transform: translateY(100%);
  transition: transform 200ms;
  will-change: transform;
}

/* ------------------------------- Slide down ------------------------------- */
.slide-down-enter {
  transform: translateY(-100%);
}
.slide-down-enter-active {
  transform: translateY(0%);
  transition: transform 200ms;
  will-change: transform;
}
.slide-down-exit {
  transform: translateY(0%);
}
.slide-down-exit-active {
  transform: translateY(-100%);
  transition: transform 200ms;
  will-change: transform;
}

/* ------------------------------- Slide start ------------------------------ */
.slide-start-enter {
  transform: translateX(100%);
}
.slide-start-enter-active {
  transform: translateX(0%);
  transition: transform 200ms;
  will-change: transform;
}
.slide-start-exit {
  transform: translateX(0%);
}
.slide-start-exit-active {
  transform: translateX(-100%);
  transition: transform 200ms;
  will-change: transform;
}

/* -------------------------------- Slide end ------------------------------- */
.slide-end-enter {
  transform: translateX(-100%);
}
.slide-end-enter-active {
  transform: translateX(0%);
  transition: transform 200ms;
  will-change: transform;
}
.slide-end-exit {
  transform: translateX(0%);
}
.slide-end-exit-active {
  transform: translateX(100%);
  transition: transform 200ms;
  will-change: transform;
}

/* --------------------------------- Zoom in -------------------------------- */
.zoom-in-enter {
  opacity: 0;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(0.8);
}
.zoom-in-enter-active {
  opacity: 1;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1);
  transition: opacity 200ms, transform 200ms;
  will-change: opacity, transform;
}
.zoom-in-exit {
  opacity: 1;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1);
}
.zoom-in-exit-active {
  opacity: 0;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(0.8);
  transition: opacity 200ms, transform 200ms;
  will-change: opacity, transform;
}
.zoom-in-enter-done {
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

/* -------------------------------- Zoom out -------------------------------- */
.zoom-out-enter {
  opacity: 0;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1.2);
}
.zoom-out-enter-active {
  opacity: 1;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1);
  transition: opacity 200ms, transform 200ms;
  will-change: opacity, transform;
}
.zoom-out-exit {
  opacity: 1;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1);
}
.zoom-out-exit-active {
  opacity: 0;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1.2);
  transition: opacity 200ms, transform 200ms;
  will-change: opacity, transform;
}
.zoom-out-enter-done {
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}
