@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('animation');

@layer base {
  :root {
    --h-toolbar: 80px;
    --ios-safe-space: 0px;
    --h-screen-constant: calc(
      100vh - constant(safe-area-inset-top) - constant(safe-area-inset-bottom) -
        var(--ios-safe-space)
    );
    --h-screen-env: calc(
      100vh - env(safe-area-inset-top) - env(safe-area-inset-bottom) -
        var(--ios-safe-space)
    );
  }
  .iPhone-X-Xs:root,
  .iPhone-Xmax-Xr:root,
  .iPhone-12:root {
    --ios-safe-space: 81px;
  }
  body {
    /* padding-top: constant(safe-area-inset-top);
    padding-top: env(safe-area-inset-top);
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom); */
    height: 100vh;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  ,
  main {
    padding-top: constant(safe-area-inset-top);
    padding-top: env(safe-area-inset-top);
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);
    height: var(--h-screen-constant);
    height: var(--h-screen-env);
    overflow: auto;
  }

  /* input[type="date"]::-webkit-inner-spin-button,
  input[type="date"]::-webkit-calendar-picker-indicator {
    display: none;
    -webkit-appearance: none;
  } */
}

@layer utilities {
  .h-screen {
    height: var(--h-screen-constant);
    height: var(--h-screen-env);
  }
  .min-h-screen {
    min-height: var(--h-screen-constant);
    min-height: var(--h-screen-env);
  }
  .h-content {
    height: calc(var(--h-screen-constant) - var(--h-toolbar));
    height: calc(var(--h-screen-env) - var(--h-toolbar));
  }
  .min-h-content {
    min-height: calc(var(--h-screen-constant) - var(--h-toolbar));
    min-height: calc(var(--h-screen-env) - var(--h-toolbar));
  }
  .scrollbar-hide {
    overflow: auto;
  }
  .container {
    @apply w-[calc(100vw-40px)] 2xl:w-8/12 xl:w-7/12 lg:w-9/12 mx-auto;
  }
  .container-mini {
    @apply w-[calc(100vw-40px)] max-w-md mx-auto;
  }
}

@layer components {
  .switch {
    /* layout */
    @apply w-12 h-6 border-0 rounded-full shadow-inner appearance-none;

    /* color */
    @apply bg-gray-200;

    /* misc */
    @apply relative focus:outline-none;
    touch-action: pan-x;

    /* animation */
    @apply transition duration-300 ease-in-out;
  }

  .switch:checked {
    @apply bg-primary text-primary-700;
    background-image: none;
  }

  .switch:focus {
    /* @apply primary-ring; */
    @apply ring-0;
  }

  .switch:disabled {
    @apply opacity-50;
  }

  .switch:before {
    /* color */
    @apply bg-white;

    /* layout */
    @apply w-5 h-5 rounded-full shadow;

    /* position */
    @apply absolute top-0.5 left-0.5;
    content: '';

    /* animation */
    @apply transition-transform duration-300 ease-in-out;
  }

  .switch:checked:before {
    @apply translate-x-6;
  }
}
